dmx.Component('f7-app', {

  initialData: {
    id: 'io.framework7.testapp',
    name: 'Framework7',
    version: '1.0.0',
    language: '',
    theme: 'auto',
    darkTheme: false,
    initialized: false,
    device: null,
    online: false,
  },

  attributes: {
    name: {
      type: String,
      default: 'Framework7',
    },

    version: {
      type: String,
      default: '1.0.0',
    },

    theme: {
      type: String,
      default: 'auto',
      enum: ['auto', 'ios', 'md', 'aurora'],
    },

    language: {
      type: String,
      default: undefined, // default equal to the current browser/webview language
    },

    autoDarkTheme: {
      type: Boolean,
      default: false,
    },

    params: {
      type: Object,
      default: {},
    },
  },

  methods: {
    alert (msg) {
      if (!this._component) return;
      this._component.dialog.alert(msg);
    },

    showPreloader (color) {
      if (!this._component) return;
      this._component.preloader.show(color);
    },

    hidePreloader () {
      if (!this._component) return;
      this._component.preloader.hide();
    },

    showProgress (color) {
      if (!this._component) return;
      this._component.progressbar.show(color);
    },

    setProgress (progress, duration) {
      if (!this._component) return;
      this._component.progressbar.set(progress, duration);
    },

    hideProgress (color) {
      if (!this._component) return;
      this._component.progressbar.hide();
    }
  },

  events: {
    init: Event,
    resize: Event,
    orientationchange: Event,
    online: Event,
    offline: Event,
  },

  render (node) {
    let theme = this.props.theme;
    let urlParams = new URLSearchParams(document.location.search);

    if (theme == 'auto' && urlParams.has('theme')) {
      theme = urlParams.get('theme');
    }

    const app = this._component = dmx.f7.init(node, {
      ...this.props.params,
      id: `io.framework7.${this.name}`,
      name: this.props.name,
      version: this.props.version,
      theme: theme,
      autoDarkTheme: this.props.autoDarkTheme,
      view: { stackPages: true },
    });

    this._registerEvents(['init', 'resize', 'orientationchange', 'online', 'offline']);

    app.on('connection', (isOnline) => {
      this.set('online', isOnline);
    });

    app.on('darkThemeChange', (isDarkTheme) => {
      this.set('darkTheme', isDarkTheme);
    });

    this.set({
      id: app.id,
      name: app.name,
      version: app.version,
      language: app.language,
      theme: app.theme,
      darkTheme: app.darkTheme,
      initialized: true,
      device: dmx.clone(app.device),
      online: app.online,
    });

    this.$parse();
  },

  _registerEvents (events) {
    for (const event of events) {
      this._component.on(event, () => this.dispatchEvent(event));
    }
  },
  
});